import React from "react";
import {connect} from 'react-redux';
const VoteMain = function  VoteMain(props) {
    let {supNum,oppNum} = props;
    return(
        <div className="main">
            <p>{supNum}</p>
            <p>{oppNum}</p>
        </div>
    )
}
export default connect(state=>state.vote)(VoteMain);
